@font-face {
  font-family: "SegoeUIBold";
  src: url("../public/fonts/SegoeUIBold/SegoeUIBold.eot");
  src: url("../public/fonts/SegoeUIBold/SegoeUIBold.eot?#iefix")
      format("embedded-opentype"),
    url("../public/fonts/SegoeUIBold/SegoeUIBold.woff") format("woff"),
    url("../public/fonts/SegoeUIBold/SegoeUIBold.ttf") format("truetype");
}

@font-face {
  font-family: "SegoeUIRegular";
  src: url("../public/fonts/SegoeUIRegular/SegoeUIRegular.eot");
  src: url("../public/fonts/SegoeUIRegular/SegoeUIRegular.eot?#iefix")
      format("embedded-opentype"),
    url("../public/fonts/SegoeUIRegular/SegoeUIRegular.woff") format("woff"),
    url("../public/fonts/SegoeUIRegular/SegoeUIRegular.ttf") format("truetype");
}
body {
  margin: 0;
  font-family: "SegoeUIRegular", sans-serif;
}
* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*::after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}
nav,
footer,
header,
aside {
  display: block;
}
html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  background-blend-mode: screen;
}
input,
textarea,
button {
  font-family: inherit;
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
li {
  list-style: none;
}
li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}
